    #b149ff 56.92%,    #e54ff2 87.66%,    #ff206e 128.2%  );  background-clip: text;  -webkit-background-clip: text;  color: transparent;  -webkit-text-fill-color: transparent;}.plugin-wrapper .custom-dropdown-in {  position: absolute;  opacity: 0;  top: 100%;  left: 0;  right: 0;  font-weight: 500;  font-size: 14px;  line-height: 16px;  border: 1px solid #e4e8f5;  box-shadow: 0px 1px 4px rgba(17, 24, 51, 0.06);  background-color: white;  display: block;  width: 100%;  color: #111833;  z-index: -1;  transform: translateY(10px);  transition: all ease 220ms;  transition-delay: 200ms;  visibility: hidden;  border-radius: 0 0 8px 8px;  border-top: none;}.plugin-wrapper .custom-btn {  font-weight: 500;  font-size: 14px;  line-height: 16px;  border: 1px solid #e4e8f5;  box-shadow: 0px 1px 4px rgba(17, 24, 51, 0.06);  border-radius: 8px;  background-color: white;  display: block;  padding: 13px 16px;  width: 100%;  color: #111833;  letter-spacing: -0.08px;  position: relative;  transition: all ease 220ms;  transition-delay: 300ms;}.plugin-wrapper .custom-btn:after {  content: "";  position: absolute;  right: 16px;  top: 16px;  border-top: 2px solid #737da5;  border-right: 2px solid #737da5;  width: 8px;  height: 8px;  transform: rotate(135deg);  transition: all ease 220ms;  transition-delay: 300ms;}.plugin-wrapper .custom-dropdown {  position: relative;}.plugin-wrapper .custom-dropdown.show .custom-dropdown-in {  opacity: 1;  transform: translateY(-1px);  transition-delay: 0ms;  z-index: 10;  visibility: visible;}.plugin-wrapper .custom-dropdown.show .custom-btn {  border-radius: 8px 8px 0 0;  border-bottom-color: transparent;  transition-delay: 0ms;}.plugin-wrapper .custom-dropdown.show .custom-btn:after {  transform: rotate(-45deg) translateY(2px);  transition-delay: 0ms;}.plugin-wrapper .cdi-in {  position: relative;  padding: 10px 16px;  border-bottom: 1px solid #e4e8f5;  z-index: 10;  overflow: hidden;  cursor: pointer;}.plugin-wrapper .cdi-in:hover {  background: #faf2fa;}.plugin-wrapper .cdi-in:last-child {  border-bottom: none;}.plugin-wrapper .cdi-in p {  font-size: 14px;  color: #111833;  margin-bottom: 4px;}.plugin-wrapper .cdi-in p:after {  content: "";  position: absolute;  top: 0;  bottom: 0;  left: 0;  right: 0;  background-color: #f8f8fc;  z-index: -1;  display: none;}.plugin-wrapper .cdi-in span {  font-size: 12px;  color: #737da5;  display: flex;  justify-content: space-between;}.plugin-wrapper .cdi-in span b {  color: #566df2;}.plugin-wrapper .cdi-in input {  position: absolute;  top: 0;  bottom: 0;  left: 0;  right: 0;  opacity: 0;  width: 100%;  height: 100%;  cursor: pointer;}.plugin-wrapper .cdi-in input:checked ~ p:after {  display: block;}.plugin-wrapper .launched-tick {  text-align: center;  margin-bottom: 36px;}.plugin-wrapper .launched-tick span {  display: inline-flex;  align-items: center;  justify-content: center;  border-radius: 50%;  width: 76px;  height: 76px;  background: linear-gradient(    119.87deg,    #4966ff 7.95%,    #7760ff 23.97%,    #b149ff 45.61%,    #e54ff2 72.38%,    #ff206e 107.68%  );  transition: all cubic-bezier(0.21, 0.74, 0.63, 0.95) 500ms;  transform: scale(0);  padding: 24px;}.plugin-wrapper .launched-tick span img {  transition: all ease 400ms;  transform: scale(0) rotate(45deg);  transition-delay: 200ms;}.plugin-wrapper .launched-tick.show span {  transform: scale(1);}.plugin-wrapper .launched-tick.show span img {  transform: scale(1) rotate(0deg);}.plugin-wrapper .launched-tick.show .confetti img {  transform: scale(1) translateY(0px);  transition-delay: 250ms;}.plugin-wrapper .launched-body {  position: relative;  text-align: center;}.plugin-wrapper .launched-body h4 {  margin-bottom: 12px;}.plugin-wrapper .launched-body h6 {  margin-bottom: 28px;}.plugin-wrapper .launched-body .btn {  padding: 8px 10px;  max-width: 210px;  border-radius: 10px;}.plugin-wrapper .confetti {  margin-bottom: -80px;}.plugin-wrapper .confetti.hide {  opacity: 0;  transition: all ease 700ms;}.plugin-wrapper .confetti img {  transform: scale(0) translateY(10px);  transition: all cubic-bezier(0, 0.29, 0.52, 1.01) 400ms;}.plugin-wrapper .login-body {  position: relative;  text-align: center;  padding-bottom: 40px;}.plugin-wrapper .login-body h3 {  background: linear-gradient(    89.52deg,    #4966ff 13.67%,    #7760ff 32.07%,    #b149ff 56.92%,    #e54ff2 87.66%,    #ff206e 128.2%  );  background-clip: text;  -webkit-background-clip: text;  color: transparent;  -webkit-text-fill-color: transparent;  text-align: center;  font-size: 22px;  margin-bottom: 16px;}.plugin-wrapper .login-body p {  line-height: 20px;  text-align: center;  letter-spacing: -0.08px;  max-width: 247px;  margin: 0 auto 28px;  color: #737da5;  font-weight: 400;}.plugin-wrapper .login-body .btn {  font-weight: 500;  font-size: 14px;  line-height: 24px;  letter-spacing: -0.24px;  margin-bottom: 20px;}.plugin-wrapper .login-body .btn span {  color: white;  font-weight: 700;  margin-left: 5px;}.plugin-wrapper .login-body .btn span img {  margin-left: 3px;}.plugin-wrapper .login-body .link {  font-weight: 500;  font-size: 14px;  line-height: 24px;  text-align: center;  letter-spacing: -0.24px;  color: #737da5;}.plugin-wrapper .login-body .link span {  color: #566df2;  margin-left: 5px;  font-weight: 700;}.plugin-wrapper .login-body .link span img {  margin-left: 3px;}.plugin-wrapper .i_infos {  width: 23px;  height: 23px;  background: #edf0ff;  display: inline-block;  font-weight: bold;  font-style: italic;  font-size: 17px;  text-align: center;  border-radius: 50%;  color: #566DF2;  border: 1px solid #999;  margin-left: 5px;  cursor: default;  transform: scale(0.7);}
